@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@200&display=swap);
body {
  margin: 0;
  font-family: 'JetBrains Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/portfolio/static/media/background.f44a8ba0.png);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.about-main{
    color:#AFAFFD;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 50%;
    margin-left: 5%;
   -webkit-align-items: flex-start;
           align-items: flex-start;
   font-size: 1.3rem;

}


ul{
    list-style: none;
}

.about-list-element{
    border-left: whitesmoke solid 1px;
    padding: 8px;
}

.linkedin{
    -webkit-filter:  brightness(0) invert(1);
            filter:  brightness(0) invert(1);
}
.navbar{
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 2rem;
    color: whitesmoke;
    margin:auto;
    padding: 2rem;
    font-size: 2rem;
    max-width: 100%;
    grid-gap: 1rem;
    gap: 1rem;
}
 a, a:hover, a:focus, a:active {
     text-decoration: none;
     color: inherit;
     
 }
 .active{
   font-weight: 1200;
   font-size: 2.3rem;
     
 }
.skills-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 5rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 3rem;
    
}
.skills-logo{
    cursor: pointer;
}
.skills-logo:hover{
    scale: 1.5;
}

.project-container{
    max-width: 60%;
    margin-top: 3rem;
    margin-left: auto;
    margin-right: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 1rem   ;
    gap: 1rem   ;
    padding:1rem;
    background: #2e2e2e;
}
.title{
    font-size: 2rem;
    font-weight: bold;
    height: 5%;
}
.project{
    width: 100%;
    max-height: 65rem;
    background: #646464;
    border-radius: 8px;
    box-shadow: 0px 7px 10px rgba(0,0,0,0.5);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    grid-gap:10px;
    gap:10px
}
.project-img{
    max-width: 100%;
    max-height:70%;
    border: whitesmoke 0.5px solid;
    border-radius: 8px;
    box-shadow: 0px 7px 10px rgba(0,0,0,0.5);
}

.project-description{
    font-size: larger;
    text-align: center;
}
.technology{
    height: 2.8rem;
}

.project-links{
    display: -webkit-flex;
    display: flex;
    grid-gap: 5px ;
    gap: 5px ;
}

.live-button{
    border:none;
    border-radius: 5px;
    cursor: pointer;
}
.live-button:hover{
    background-color: lightslategrey;
}
@media only screen and (max-width: 600px) {
    .project-container{
        max-width: 90%;
        margin-top: 3rem;
        margin-left: auto;
        margin-right: auto;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        grid-gap: 1rem   ;
        gap: 1rem   ;
        padding:1rem;
        background: #2e2e2e;
    }
    .title{
        font-size: 1.3rem;
        font-weight: bold;
        height: 5%;
    }

}
